.App {
    height: 100vh;
    width: 100vw;
    background: #BE93C5;
    background: -webkit-linear-gradient(to right, #3bbfc8, #BE93C5);  
    background: linear-gradient(to right, #33a8b1, #BE93C5); 
    justify-content: center;
    align-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}      

.dash-container {
  overflow-y: auto;
  overflow-x: hidden;
  flex: auto;
}

@font-face{
  font-family: roboto;
  src: url('./Assets/static/RobotoSlab-Medium.ttf');
}

@media screen and (min-width: 601px) {

  .login-box-shadow {
    -webkit-box-shadow: 8px 11px 26px -2px #000000; 
    box-shadow: 8px 11px 26px -2px #000000;
    border-radius: 20px;
    height: 63vh;
    width: 40vh;
    overflow: hidden;
    font-family: roboto;
  }    

  .footer {
    height: 10vh;
  }

  .column {
    width: 26vw;
    height: 30vh;
    border-radius: 20px;
    padding: 0;
    margin: 15px 0px 0px;
    background-size: cover;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .right-header {
    flex: 1;
    padding-right: 10px;
    display: flex;
    justify-content: flex-end;
  }

  .centered {
    font-family: roboto;
    padding: 60px 60px 0;
    display: flex;
    align-content: center;
    justify-content: center;
    flex-direction: column;
  }
  
}

@media screen and (max-width: 601px) {

  .login-box-shadow {
    -webkit-box-shadow: 8px 11px 26px -2px #000000; 
    box-shadow: 8px 11px 26px -2px #000000;
    border-radius: 20px;
    height: 63vh;
    width: 40vh;
    overflow: hidden;
    font-size: 1.6rem !important;
    font-family: roboto;
  }   

  .footer {
    height: 7vh;
  }
  
  .column {
    width: 85vw;
    height: 30vh;
    margin: 15px 0px 0px;
    border-radius: 20px;
    padding: 0;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .right-header {
    flex: 1;
    padding-right: 1px;
    display: flex;
    justify-content: flex-end;
  }

  .centered {
    font-family: roboto;
    padding: 80px 80px 0;
    display: flex;
    align-content: center;
    justify-content: center;
    flex-direction: column;
  }

  .centered span {
    font-size: 1.5rem;
  }
  
}

.logout {
  align-self: center;
}

.tab-button {
  font-size: 1.2rem;
  margin: 0 5px;
}

.google-button {
  background-color: #FFFF;
  font-size: 1rem;
  font-family: roboto, serif;
  color: gray;
  border-width: 1px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-content: center;
  flex-direction: row;
  cursor: pointer;
  box-shadow: 2px 15px 17px -17px #000000;  
  border-radius: 2px;
  outline: none;
  padding-left: 2px;
}

.google-button:hover {
  background-color: royalblue;
  color: white;
}

.span {
  margin-top: 5px;
  margin-left: 8px;
  display: inline-block;
}

.header {
  z-index: 10;
  flex: none;
  display: flex;
  position: fixed;
  margin: 0;
  top: 0;
  left: 0;
  align-items: center;
  background: linear-gradient(90deg,#9198e5, #34A8B1);}

.logo {
  font-family: roboto;
  font-size: 1.5rem;
  margin-top: 4px;
}

.name {
  color: #dd17c3;
}

.tab-button {
  cursor: pointer !important;
  font-family: roboto;
  border: none;
  outline: none;
  background: none;
}

.active {
  color: #dd17c3;
}

.tab-button:hover {
  color: #dd17c3;
}

.footer {
  flex: none;
  width: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  text-align: center;
  bottom: 0;
  left: 0;
}

.footer span {
  align-self: center;
  margin: 0 10px;
  font-size: 1.3rem;
}

.footer button {
  outline: none;
  border: none;
  background-color: #dd17c3;
  padding: 0;
  height: 50px;
  width: 50px;
  border-radius: 50px;
  align-self: center;
  cursor: pointer;
}

.card-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
  justify-content: space-around;
  padding: 60px 0;
  min-height: 400px;
}

.p-row {
  width: 100%;
  background-color: rgba(10, 10, 10, 0.5);
  color: white;
  bottom: 0;
  left: 0;
  padding: 0;
  margin-bottom: 0;
  height: fit-content;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  text-align: center;
  padding: 2px 0;
}

.search-box {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  padding-top: 20px;
}

.search-box button {
  background-color: #3374d6;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  text-transform: capitalize;
  outline: none;
  min-width: 220px;
  border-radius: 0;
}

.setButton{ 
  margin-top: 10px;
  margin-left: 10px;
  border-radius: 3px !important;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  margin-top: 70px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.drop-header {
  position: relative;
  display: inline-block;
}

.openDrop {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: fit-content;
  max-height: 20vh;
  background-color: #f9f9f9;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  overflow-y: auto;
  margin-left: 10px;
}

